


















import Vue from "vue";
import { Component } from "vue-property-decorator";
import Tabs from "../components/Tabs.vue";
import intervaList from "../constant/interval";
import recordTypeList from "../constant/recordTypeList";
import dayjs from "dayjs";
import clone from "@/lib/clone";

const oneday = 86400 * 1000;
@Component({
  components: { Tabs },
})
export default class Statistics extends Vue {
  tagString(tags: Tag[]) {
    return tags.length == 0 ? "无" : tags.join(",");
  }
  beautify(string: string) {
    const now = dayjs();
    if (dayjs(string).isSame(now, "day")) {
      return "今天";
    } else if (dayjs(string).isSame(now.subtract(1, "day"), "day")) {
      return "昨天";
    } else if (dayjs(string).isSame(now.subtract(2, "day"), "day")) {
      return "前天";
    } else if (dayjs(string).isSame(now, "year")) {
      return dayjs(string).format("M月D日");
    } else {
      return dayjs(string).format("YYYY年M月D日");
    }
  }
  get recordList() {
    return (this.$store.state as RootState).recordList;
  }
  get groupedList() {
    const { recordList } = this;
    if (recordList.length == 0) {
      return [];
    }
    const newList = clone(recordList).filter(r=>r.type==this.type).sort(
      (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
    );
    type Result = {title: string;total?: number;items: RecordItem[]}[]
    const result: Result = [
      {
        title: dayjs(newList[0].createdAt).format("YYYY-MM-DD"),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdAt).format("YYYY-MM-DD"),
          items: [current],
        });
      }
    }
    result.map(group=>{
      group.total = group.items.reduce((sum,item)=>sum+item.amount,0)
    })
    return result;
  }
  beforeCreate() {
    this.$store.commit("fetchRecords");
  }
  type = "-";
  recordTypeList = recordTypeList;
}
